<template>
	<view class="toast" v-if="isShow" >
		<view class="toast-icon" v-if="icon && isload==false">
			<text :class="icon" class="toast-icon-e"></text>
		</view>
		<view class="toast-icon-load" v-if="isload">
			<text class="iconfont icon-91jiazai load" ></text>
		</view>
		<view class="toast-text">
			<template v-if="messgae">
				{{messgae}}
			</template>
			<template v-else>
				<slot></slot>
			</template>
		</view>
	</view>
</template>


<script>
	export default{
		props:{
		},
		data(){
			return {
				messgae:this.msg,
				icon:null,
				isAuto:false,
				isShow:false,
				item:2000,
				isload:false,
				animation:'',
				id:''			}
		},
		methods:{
			// 创建子组件的方法
			createTotal({msg,icon,item,isAuto = false,isload = false}){
				clearInterval(this.id)
				if(msg!=null) this.messgae = msg;
				this.isShow = true
				this.icon = icon
				this.isAuto = isAuto
				this.isload = isload
				
				if(this.isAuto){
					this.autoShow(item)
				}
			},
			
			// 手动销毁
			autoShow(tiem){
				tiem = undefined ? this.item : tiem
				setTimeout(()=>{
					this.isShow = false
				},3000)
			}
		},
		watch:{
			value(newValue){
				if(newValue){
					this.autoShow()
				}
			}
		}
	}
</script>


<style lang="scss">
	.toast{
		position: absolute;
		top: 50%;
		left:50%;
		transform: translate(-50%,-50%);
		background-color: rgb(94,98,101);
		color:#FFFFFF;
		padding: 10px 15px;
		border-radius: $uni-border-radius-base;
		z-index: $uni-zindex-lg;
		
		.toast-icon-load{
			text-align: center;
			.load{
				animation: xuan 2s ;
				font-size: 55px;
			
			}
			animation: xuan 5s infinite;
		}
		
		.toast-icon{
			text-align: center;
			.toast-icon-e{
				font-size: 30px;
			}
			
		}
	}
	
	@keyframes xuan {
		0%{
			transform: rotate(0deg);
		}
		100%{
			transform: rotate(360deg);
		}
	}
	
</style>